/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  padding: 8rem 0 0 0;
  font-family: "Lato", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
  background-color: #212121;
  color: rgba(255, 255, 255, 0.6);
}

p {
  font-size: 2rem;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

.contenedor2 {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
  color: rgba(255, 255, 255, 0.6);
}

img,
picture {
  width: 100%;
  display: block;
}

h1,
h2,
h3 {
  font-family: "Lato", sans-serif;
  margin: 2.5rem 0;
  font-weight: 300;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
  text-transform: uppercase;
  font-weight: 600;
  margin-top: 10rem;
  color: #5c6829;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

.seccion {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
}

.contenido-centrado {
  max-width: 800px;
}

.header {
  background-color: rgba(255, 255, 255, 0.5);
  backdrop-filter: blur(10px);
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 10;
}

.nav {
  display: flex;
  flex-direction: column;
}
@media (min-width: 1024px) {
  .nav {
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }
}
.nav .logo {
  width: auto;
  height: 8rem;
}

.nav_primary {
  font-weight: 600;
  font-size: 1.2rem;
  text-transform: uppercase;
  display: flex;
  flex-direction: column;
  text-align: center;
  visibility: hidden;
  opacity: 0;
  height: 0;
  transition: visibility 0.2s, opacity 0.2s linear, height 0.2s linear;
}
.nav_primary a {
  color: #212121;
}
@media (min-width: 1024px) {
  .nav_primary {
    visibility: visible;
    opacity: 1;
    height: auto;
    display: flex;
    flex-direction: row;
    gap: 1.5rem;
    justify-content: center;
  }
}
.nav_primary a {
  padding: 0.5rem 1rem;
}

.touch-bar {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.touch-bar .menu {
  width: 4rem;
  height: 4rem;
}
@media (min-width: 1024px) {
  .touch-bar .menu {
    display: none;
  }
}

.nav_secundary {
  position: fixed;
  z-index: 3;
  bottom: 2%;
  display: flex;
  flex-direction: column;
  margin: 0;
  background-color: rgba(0, 0, 0, 0.5);
  backdrop-filter: blur(10px);
  border-radius: 0rem 1rem 1rem 0rem;
}
.nav_secundary a {
  font-size: 1.3rem;
  text-transform: uppercase;
  color: #FFFFFF;
  font-weight: 600;
  padding: 1rem 3rem;
}

.menu {
  cursor: pointer;
  margin-right: 1rem;
}

.nav.show .nav_primary {
  height: auto;
  visibility: visible;
  opacity: 1;
}

.creat {
  color: #212121;
  margin: 0 10rem 10rem 10rem;
  border-radius: 2rem;
}
.creat .form {
  background-color: rgb(0, 255, 255);
  width: 100%;
  border-radius: 2rem;
}

.login-nav {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.login-nav button {
  border: none;
  padding: 4rem 3rem;
  background: rgba(0, 255, 255, 0.466);
}
.login-nav button:first-of-type {
  border-radius: 2rem 0 0 0;
}
.login-nav button:last-of-type {
  border-radius: 0 0 0 2rem;
}
.login-nav button:hover {
  background: rgb(0, 255, 255);
}

form {
  padding: 2rem;
}
form label,
form input, form select {
  display: block;
  margin: 0;
}
form label {
  font-weight: 600;
}
form input, form textarea, form select {
  width: 100%;
  padding: 1rem;
  background: transparent;
  border: 1px solid #333333;
  border-radius: 5px;
}
form select {
  font-weight: 700;
  font-size: 2rem;
}

.boton {
  margin: 3rem 0;
  padding: 1rem 2rem;
  background-color: rgb(0, 255, 34);
  border: 1px solid #FFFFFF;
  border-radius: 1rem;
}
.boton:hover {
  cursor: pointer;
  background-color: #00eb1f;
}

.error {
  text-align: center;
  margin: 1rem;
  color: red;
  font-weight: 600;
}

/** INDICE FOTOS **/
.propiedades {
  color: #212121;
  margin-top: 4rem;
  margin-bottom: 4rem;
  width: 100%;
  border-spacing: 1;
  text-align: center;
}
.propiedades thead {
  background-color: rgba(0, 255, 255, 0.466);
}
.propiedades thead tr {
  border: 1px solid black;
  font-size: 2rem;
  text-transform: uppercase;
  padding: 3rem;
}
.propiedades tbody {
  background: rgb(0, 255, 255);
}
.propiedades tbody .imagen-tabla {
  width: 15rem;
}
.propiedades tbody .botones {
  margin: 0 auto;
  padding: 1rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 2rem;
  margin: 0;
}
.propiedades tbody .contenido-tabla {
  border: 1px solid #000000;
  margin: 3rem;
}
.propiedades tbody form {
  margin: auto;
}

.imagen_small {
  margin: 2rem 0;
  width: 20rem;
}

.boton-admin-actualizar {
  margin: 0 auto;
  padding: 1rem 0;
  background-color: greenyellow;
  border-radius: 1rem;
  width: 100%;
}

.boton-admin-borrar {
  margin: 0 auto;
  border: none;
  background-color: red;
  width: 100%;
}
.boton-admin-borrar:hover {
  cursor: pointer;
}

.nav_propiedades {
  display: flex;
  justify-content: space-between;
  margin-top: 2rem;
}
.nav_propiedades a {
  padding: 1rem;
  background: rgba(0, 255, 255, 0.466);
  border-radius: 1rem;
}
.nav_propiedades a:hover {
  cursor: pointer;
  background: rgba(0, 255, 255, 0.774);
}

@media (min-width: 768px) {
  .items {
    padding: 8rem 0;
    display: grid;
    grid-template-rows: 1fr 1fr;
  }
  .items .items_horizontal {
    display: grid;
    grid-template-columns: 1fr 1fr;
  }
}
.item1 {
  --opacidad-negro: 0.2;
  background-image: linear-gradient(rgba(0, 0, 0, var(--opacidad-negro)), rgba(0, 0, 0, var(--opacidad-negro))), url("/build/img/Apartado_scenichna.webp");
}

.item2 {
  --opacidad-negro: 0.2;
  background-image: linear-gradient(rgba(0, 0, 0, var(--opacidad-negro)), rgba(0, 0, 0, var(--opacidad-negro))), url("/build/img/galeria/gal4.webp");
}

.item3 {
  --opacidad-negro: 0.2;
  background-image: linear-gradient(rgba(0, 0, 0, var(--opacidad-negro)), rgba(0, 0, 0, var(--opacidad-negro))), url("/build/img/galeria/gal1.webp");
}

.item {
  margin: 2rem;
  height: 50rem;
  width: auto;
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
  border-radius: 3rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.item .botones {
  font-size: 2rem;
  padding: 1rem;
  border-radius: 2rem;
  border: 2px solid rgba(255, 255, 255, 0.767);
}
.item p {
  margin: 0;
  text-align: center;
  text-transform: uppercase;
  font-size: 4rem;
  font-weight: 700;
  padding: 1rem;
  color: rgba(255, 255, 255, 0.767);
}

.product_list {
  display: grid;
}
@media (min-width: 768px) {
  .product_list {
    grid-template-columns: 50% 50%;
  }
}
@media (min-width: 1024px) {
  .product_list {
    grid-template-columns: 33.3% 33.3% 33.3%;
  }
}
.product_list .product {
  display: grid;
  grid-template-rows: 60% 20% 20%;
  justify-items: center;
  align-items: center;
  -webkit-box-shadow: 0px 0px 20px 4px rgba(0, 0, 0, 0.9);
  backdrop-filter: blur(7px);
  background-color: hsla(0deg, 0%, 100%, 0.3);
  height: 60rem;
  border-radius: 0.8rem;
}
.product_list .product img {
  width: auto;
  height: auto;
  max-height: 30rem;
  max-width: 100%;
  display: inline-block;
  vertical-align: middle;
  scale: 1;
  transition: all 0.5s;
}
.product_list .product h2 {
  font-size: 2.5rem;
  font-weight: 500;
  color: #9bad4c;
}
.product_list .product p {
  font-size: 3rem;
  font-weight: 600;
}
.product_list a:hover .product img {
  scale: 1.1;
}
.product_list article {
  margin: 2rem;
  padding: 2rem;
}

@media (min-width: 768px) {
  .product_description {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 2rem;
  }
}
.product_description .recuadro_imagen-des {
  position: relative;
  width: auto;
  max-height: 60rem;
  white-space: nowrap;
  text-align: center;
  font-size: 0;
  display: flex;
  justify-content: center;
  align-items: center;
}
.product_description .recuadro_imagen-des img {
  width: auto;
  max-width: 100%;
  max-height: 60rem;
  display: inline-block;
  vertical-align: middle;
  -webkit-backface-visibility: hidden;
  backface-visibility: hidden;
  transition: all 0.3s;
}
.product_description div {
  display: grid;
  margin: 5rem 0;
}
.product_description p {
  margin: 0.5rem;
}
.product_description .name {
  font-size: 3rem;
  font-weight: 600;
  color: #9bad4c;
}
.product_description .number,
.product_description .description,
.product_description .description_use {
  text-align: left;
  font-size: 1.5rem;
}
.product_description .price {
  font-size: 3rem;
  font-weight: 600;
  color: #9bad4c;
}
@media (min-width: 768px) {
  .product_description .name,
  .product_description .price {
    font-size: 4rem;
  }
  .product_description .number,
  .product_description .description,
  .product_description .description_use {
    font-size: 2rem;
  }
}

.recuadro_imagen {
  display: flex;
  justify-content: center;
  min-width: 20rem;
  min-height: 20rem;
}
.recuadro_imagen img {
  width: auto;
  height: auto;
  margin: auto;
}

.footer-pr {
  background-color: #121212;
}
.footer-pr h3 {
  padding-top: 5rem;
}

.footer {
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 6rem 3rem 6rem 0 rem;
}
@media (min-width: 768px) {
  .footer {
    flex-direction: row;
    justify-content: space-evenly;
  }
}
.footer .maps iframe {
  border-radius: 2rem;
  max-width: 30rem;
  max-height: 30rem;
}
.footer .contact {
  color: rgba(255, 255, 255, 0.6);
  font-size: 3rem;
}

.sld {
  display: flex;
  justify-content: center;
  align-items: center;
}

.slider {
  width: 100%;
  height: auto;
  overflow: hidden;
}

.slides {
  width: 500%;
  height: auto;
  display: flex;
}

.slides input {
  display: none;
}

.slide {
  width: 20%;
  transition: 2s;
}

.slide img {
  width: 150%;
  height: 50rem;
}
@media (min-width: 768px) {
  .slide img {
    width: 100%;
    height: 90rem;
  }
}

.navigation-manual {
  position: absolute;
  width: 100%;
  margin-top: -4rem;
  display: flex;
  justify-content: center;
}

.manual-btn {
  border: 2px solid #d1e089;
  padding: 0.5rem;
  border-radius: 1rem;
  cursor: pointer;
  transition: 1s;
}

.manual-btn:not(:last-child) {
  margin-right: 4rem;
}

.manual-btn:hover {
  background: #d1e089;
}

#radio1:checked ~ .first {
  margin-left: 0;
}

#radio2:checked ~ .first {
  margin-left: -20%;
}

#radio3:checked ~ .first {
  margin-left: -40%;
}

#radio4:checked ~ .first {
  margin-left: -60%;
}

#radio5:checked ~ .first {
  margin-left: -80%;
}

.navigation-auto {
  position: absolute;
  display: flex;
  width: 120rem;
  justify-content: center;
  margin-top: 460rem;
}

.navigation-auto div {
  border: 2px solid #d1e089;
  padding: 0.5rem;
  border-radius: 1rem;
  transition: 1s;
}

.navigation-auto div:not(:last-child) {
  margin-right: 4rem;
}

#radio1:checked ~ .navigation-auto.auto-btn1 {
  background: #d1e089;
}

#radio2:checked ~ .navigation-auto.auto-btn2 {
  background: #d1e089;
}

#radio3:checked ~ .navigation-auto.auto-btn3 {
  background: #d1e089;
}

#radio4:checked ~ .navigation-auto.auto-btn4 {
  background: #d1e089;
}

#radio5:checked ~ .navigation-auto.auto-btn5 {
  background: #d1e089;
}